import React from 'react';
import { Fragment } from 'react';
import { Disclosure, Menu, Transition } from '@headlessui/react';
import { Bars3Icon, XMarkIcon } from '@heroicons/react/24/outline';

import { Link } from "react-router-dom";
import { useSelector } from 'react-redux';
import { selectItems } from '../cart/cartSlice';
import { selectUserInfo } from '../user/userSlice';

const navigation = [
    { name: 'Products', link: '/', current: true, user: true },
    { name: 'About Us', link: '/about', current: false, user: true },
    { name: 'Contact Us', link: '/contact', current: false, user: true },
    { name: 'Admin Products', link: '/admin', current: false, admin: true },
    { name: 'Admin Orders', link: '/admin/orders', current: false, admin: true },
    { name: 'Admin Queries', link: '/admin/queries', current: false, admin: true }
]
const userNavigation = [
    { name: 'My Profile', link: '/profile' },
    { name: 'My Orders', link: '/my-orders' },
    { name: 'Sign out', link: '/logout' }
]

function classNames(...classes) {
    return classes.filter(Boolean).join(' ')
}

function Navbar({ children }) {
    const items = useSelector(selectItems);
    const userInfo = useSelector(selectUserInfo);

    return (
        <div>
            {userInfo && <div className="min-h-full">
                <Disclosure as="nav" className="bg-[#111827]">
                    {({ open }) => (
                        <>
                            <div className="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8">
                                <div className="flex h-16 items-center justify-between">
                                    <div className="flex items-center">
                                        <div className="flex-shrink-0">
                                            <Link to="/">
                                                <img
                                                    className="h-10 w-10"
                                                    src="/navbar-icon.png"
                                                    alt="Your Company"
                                                />
                                            </Link>
                                        </div>
                                        <div className="hidden md:block">
                                            <div className="ml-10 flex items-baseline space-x-4">
                                                {navigation.map((item) =>
                                                    item[userInfo.role] ? (
                                                        <Link
                                                            key={item.name}
                                                            to={item.link}
                                                            className={classNames(
                                                                item.current
                                                                    ? 'bg-white text-black'
                                                                    : 'text-gray-300 hover:bg-gray-700 hover:text-white',
                                                                'rounded-md px-3 py-2 text-sm font-medium'
                                                            )}
                                                            aria-current={item.current ? 'page' : undefined}
                                                        >
                                                            {item.name}
                                                        </Link>
                                                    ) : null
                                                )}
                                            </div>
                                        </div>
                                    </div>
                                    <div className="hidden md:block">
                                        <div className="ml-4 flex items-center md:ml-6">
                                            <Link to="/cart">
                                                <button
                                                    type="button"
                                                    className="relative rounded-full bg-gray-800 p-1 text-gray-400 hover:text-white focus:outline-none focus:ring-2 focus:ring-transparent focus:ring-offset-2 focus:ring-offset-gray-800"
                                                >
                                                    <span className="absolute -inset-1.5" />
                                                    <span className="sr-only">View notifications</span>
                                                    {items.length > 0 && <span className="inline-flex items-center justify-center gap-2 rounded-md bg-blue-100 px-3 py-1 font-medium text-[#1F2937] ring-1 ring-inset ring-green-600/20 outline-none border-none">
                                                        <img src="/cart-icon.png" className="h-8 w-8" aria-hidden="true" alt="Navbar Image" />
                                                        <p className='text-md font-bold -mt-3 -ml-1 py-1'>{items.length}</p>
                                                    </span>}
                                                </button>
                                            </Link>

                                            <Menu as="div" className="relative ml-3">
                                                <div>
                                                    <Menu.Button className="relative flex max-w-xs items-center rounded-full bg-gray-800 text-sm focus:outline-none focus:ring-2 focus:ring-white focus:ring-offset-2 focus:ring-offset-gray-800">
                                                        <span className="absolute -inset-1.5" />
                                                        <span className="sr-only">Open user menu</span>
                                                        <img className="h-10 w-10 rounded-full" src='/user.png' alt="" />
                                                    </Menu.Button>
                                                </div>
                                                <Transition
                                                    as={Fragment}
                                                    enter="transition ease-out duration-100"
                                                    enterFrom="transform opacity-0 scale-95"
                                                    enterTo="transform opacity-100 scale-100"
                                                    leave="transition ease-in duration-75"
                                                    leaveFrom="transform opacity-100 scale-100"
                                                    leaveTo="transform opacity-0 scale-95"
                                                >
                                                    <Menu.Items className="absolute right-0 z-10 mt-2 w-48 origin-top-right rounded-md bg-white py-1 shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none">
                                                        {userNavigation.map((item) => (
                                                            <Menu.Item key={item.name}>
                                                                {({ active }) => (
                                                                    <Link
                                                                        to={item.link}
                                                                        className={classNames(
                                                                            active ? 'bg-gray-100' : '',
                                                                            'block px-4 py-2 text-sm text-gray-700'
                                                                        )}
                                                                    >
                                                                        {item.name}
                                                                    </Link>
                                                                )}
                                                            </Menu.Item>
                                                        ))}
                                                    </Menu.Items>
                                                </Transition>
                                            </Menu>
                                        </div>
                                    </div>
                                    <div className="-mr-2 flex md:hidden">
                                        <Disclosure.Button className="relative inline-flex items-center justify-center rounded-md bg-gray-800 p-2 text-gray-400 hover:bg-gray-700 hover:text-white focus:outline-none focus:ring-2 focus:ring-white focus:ring-offset-2 focus:ring-offset-gray-800">
                                            <span className="absolute -inset-0.5" />
                                            <span className="sr-only">Open main menu</span>
                                            {open ? (
                                                <XMarkIcon className="block h-6 w-6" aria-hidden="true" />
                                            ) : (
                                                <Bars3Icon className="block h-6 w-6" aria-hidden="true" />
                                            )}
                                        </Disclosure.Button>
                                    </div>
                                </div>
                            </div>

                            <Disclosure.Panel className="md:hidden">
                                <div className="space-y-1 px-2 pb-3 pt-2 sm:px-3">
                                    {navigation.map((item) =>
                                        item[userInfo.role] ? (
                                            <Link
                                                key={item.name}
                                                to={item.link}
                                                className={classNames(
                                                    item.current
                                                        ? 'bg-white text-black'
                                                        : 'text-gray-300 hover:bg-gray-700 hover:text-white',
                                                    'rounded-md px-3 py-2 text-sm font-medium'
                                                )}
                                                aria-current={item.current ? 'page' : undefined}
                                            >
                                                {item.name}
                                            </Link>
                                        ) : null
                                    )}
                                </div>
                                <div className="border-t border-gray-700 pb-3 pt-4">
                                    <div className="flex items-center px-5">
                                        <div className="flex-shrink-0">
                                            <img className="h-12 w-12 rounded-full" src='/user.png' alt="" />
                                        </div>
                                        <div className="ml-3">
                                            <div className="text-base font-medium leading-none text-white">{userInfo.name}</div>
                                            <div className="text-sm font-medium leading-none text-gray-400">{userInfo.email}</div>
                                        </div>
                                        <Link to="/cart">
                                            <button
                                                type="button"
                                                className="relative ml-auto flex-shrink-0 rounded-full bg-gray-800 p-1 text-gray-400 hover:text-white focus:outline-none focus:ring-2 focus:ring-transparent focus:ring-offset-2 focus:ring-offset-gray-800"
                                            >
                                                <span className="absolute -inset-1.5" />
                                                <span className="sr-only">View notifications</span>
                                                {items.length > 0 && <span className="inline-flex items-center justify-center gap-2 rounded-md bg-[#b0d2ff] px-2 py-1 font-medium text-[#1F2937] ring-1 ring-inset ring-green-600/20 outline-none border-none mx-3">
                                                    <img src="/cart-icon.png" className="h-8 w-8" aria-hidden="true" alt="Navbar Image" />
                                                    <p className='text-md font-bold -mt-3 -ml-1 py-1'>{items.length}</p>
                                                </span>}
                                            </button>
                                        </Link>
                                    </div>
                                    <div className="mt-3 space-y-1 px-12">
                                        {userNavigation.map((item) => (
                                            <Disclosure.Button
                                                key={item.name}
                                                className="block rounded-md px-3 py-2 text-base font-medium text-gray-400 border-2 border-gray-400 w-full my-5"
                                            >
                                                <Link
                                                    to={item.link}
                                                    className={classNames(
                                                        'block px-4 py-2 text-sm text-gray-200'
                                                    )}
                                                >
                                                    {item.name}
                                                </Link>
                                            </Disclosure.Button>
                                        ))}
                                    </div>
                                </div>
                            </Disclosure.Panel>
                        </>
                    )}
                </Disclosure>

                <header className="bg-white shadow">
                    <div className="mx-auto max-w-7xl px-4 py-3 sm:px-6 lg:px-8">
                        <h1 className="text-3xl font-bold tracking-tight text-gray-900">Vendr</h1>
                    </div>
                </header>
                <main>
                    <div className="mx-auto max-w-7xl py-6 sm:px-6 lg:px-8">{children}</div>
                </main>
            </div>}
        </div>
    )
}

export default Navbar;
